@import '../../assets/styles/variables';
@import "../../assets/styles/mixins/container";

.root {
  width: 100%;
  height: 80px;
  position: fixed;
  transition: background-color ease-in-out 0.2s;
  z-index: 2;

  &__fixed {
    background-color: rgba(250, 247, 242, 0.9);
    -webkit-box-shadow: 0 4px 6px 0 rgba(12, 0, 46, 0.05);
    box-shadow: 0 4px 6px 0 rgba(12, 0, 46, 0.05);
    backdrop-filter: blur(8px);
  }

  &__container {
    @include Container;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 4rem;
  }

  &__menu {
    display: flex;
    flex-grow: 1;
    align-items: center;
    column-gap: 2rem;
  }

  &__link {
    text-transform: uppercase;
    & a {
      font-size: 16px;
      color: $header-color;
      transition: color ease-in-out 0.2s;

    }

    & a:hover {
      color: $secondary-color;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    column-gap: 2rem;

    & span {
      color: $header-color;
      font-weight: 500;
    }
  }

  &__phone {
    display: flex;
    align-items: center;
    column-gap: 1rem;
  }

}