.root {
  position: relative;
  max-width: 306px;
  width: 100%;
  height: 56px;
  background: #fff;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  padding: 12px 19px;
}
.root input {
  appearance: none;
  border: none;
  width: 100%;
}
.root svg {
  fill: #fdc913;
  height: 35px;
  width: 35px;
  cursor: pointer;
}

