@import '../libs/normalize';
@import '../variables';
@import '../mixins/button';

.button {
    @include Button;

    &--circle {
        @include Button--circle;
    }

    &--black {
        @include Button--black;
    }

    &--outline {
        @include Button--outline;
    }

    &__delimiter {
        width: 1px;
        height: 25px;
        background-color: rgba(255, 255, 255, 0.25);
        margin-left: 14px;
        margin-right: 14px;
    }

    &--add {
        height: 45px;

        svg {
            margin-right: 2px;
        }

        span {
            font-weight: 600;
            font-size: 16px;
        }

        &:hover {
            i {
                background-color: #fff;
                color: $orange;
            }
        }

        i {
            display: inline-block;
            border-radius: 50%;
            background-color: $orange;
            color: #fff;
            font-weight: 600;
            width: 22px;
            height: 22px;
            font-style: normal;
            font-size: 13px;
            line-height: 22px;
            position: relative;
            top: -1px;
            left: 3px;
        }
    }

    &--cart {
        display: flex;
        align-items: center;
        line-height: 23px;
        padding: 12px 25px;
        min-width: auto;

        svg {
            margin-right: 8px;
            margin-bottom: 1px;
        }

        span {
            font-weight: 600;
            font-size: 16px;
            white-space: nowrap;
        }
    }
}
