.root {
  display: flex;
  align-items: center;
  column-gap: 1.5rem;
  position: relative;
}
.root div {
  position: absolute;
  bottom: 14px;
  left: 22px;
  width: 24px;
  height: 24px;
  background: #fdc913;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #292929;
}

